<template>
	<div v-if="id <= qNum">
		<p>{{question}}</p>
		<quizOptions v-for="option in options" :optionText="option.option"></quizOptions>
		<p>{{id}}</p>
		<p>{{qNum}}</p>
	</div>
</template>

<script>
	import QuizOptions from './QuizOptions.vue'

	export default {
		props: ['question', 'id', 'options', 'qNum'],
		data: function () {
			return {

			}
		},
		components: {
			quizOptions: QuizOptions
		}
	}
</script>

<style lang="scss"></style>


